import s from "./ContactForm.module.css";
import { Component } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

class ContactFrom extends Component {

    state={
        name: '',
        number: '',
    }

    static propTypes = {
        getDataFunc: PropTypes.func.isRequired,
    }

    inputHandler = e => {
        const {name, value} = e.currentTarget
        this.setState({[name]: value})
    }

    submitHandler = e => {
        e.preventDefault();
        this.reset();
        this.props.getDataFunc(this.state)
    }

    reset = () => {
        this.setState({name: '', number: ''})
    }

    render() {
        const {name, number} = this.state;
        const {inputHandler, submitHandler} = this;
        return (
            <form className={s.form} onSubmit={submitHandler}>
                <label className={s.label}>Name: 
                    <input type="text" name="name" value={name} onChange={inputHandler} className={s.input}></input>
                </label>
                <label className={s.label}>Number: 
                    <input type="text" name="number" value={number} onChange={inputHandler} className={s.input}></input>
                </label>
                <button type="submit" className={s.btn}>Add contact</button>
            </form>
        )
    }
}

export default ContactFrom;